@import url(https://fonts.googleapis.com/css2?family=Urbanist:wght@300;400;500;600;700;800;900&display=swap);
body {
  font-size: 16px !important;
}

* {
  font-family: 'Urbanist';
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

#navContainer {
    border-bottom: 5px solid lightcoral;
    display: flex;
    align-items: center;
    align-content: center;
    height: 70px;
}
#header {
    margin-left: 2rem;
    display: flex;
    flex-grow: 1;
    align-items: center;
    align-content: center;
}
#headerLabel1 {
    display: inline-block;
    font-size: 1.4rem;
    font-weight: 600;
    margin-left: 1rem;
    color: rosybrown;
}
#headerLabel2 {
    display: none;
    font-size: 1.4rem;
    font-weight: 800;
    margin-left: 1rem;
    color: rosybrown;
}
#options {
    float: right;
    margin-right: 7vh;
}
.optionsButton {
    font-weight: 600;
    font-size: 20px;
    color: dimgrey;
}
.selectedIndicator {
    height: 10px;
    background-color: white;
    position: absolute;
    left: 0;
    right: 0;
    bottom: -20px;
}
#optionsDropdown {
    display: none;
    margin-right: 1.5rem;
    font-size: 20px;
    float: right;
}
.menuButton {
    font-weight: 500;
    color: lightcoral;
}

@media only screen and (max-width: 768px) {
    #optionsDropdown {
        display: block;
    }
    #options {
        display: none;
    }
    #headerLabel1 {
        display: none;
    }
    #headerLabel2 {
        display: inline-block;
    }
}
#title {
    font-weight: 600;
    font-size: 4rem;
}

@media only screen and (max-width: 768px) {
    #title {
        font-size: 3rem;
    }
}
#projects-search {
    width: 30%;
    float: right;
}

#projects-container {
    margin-top: 1.2rem;
    margin-bottom: 2.2rem;
    grid-gap: 2rem;
    gap: 2rem;
    overflow: auto;
    flex-wrap: wrap;
    display: flex;
    flex-direction: row;
}

@media only screen and (max-width: 768px) {

    #projects-search {
        width: 50%;
    }

    #projects-container {
        flex-wrap: nowrap;
    }

}
#contact-root {
    display: flex;
    flex-direction: row;
    padding: 12% 12% 12% 12%;
}

@media only screen and (max-width: 768px) {
    #contact-root {
        flex-direction: column-reverse;
    }
}
